.login-form-container {
  width: 450px;
  height: 500px;
  margin: auto;
  background-color: white;
  padding: 32px;
  display: flex;
  margin-top: 5em;
  flex-direction: column;
  align-self: center;
  justify-content: center;
  text-align: center;
}

.login-form {
  margin: 48px auto;
}

.video-wrapper {
  background-color: #1f1f23;
  border-radius: 8px;
  display: flex;
}
.video-commands-overlay {
  position: absolute;
  right: 0;
  z-index: 999;
}

.video-loading-spinner {
  margin: auto;
}

.video-loading-spinner-span {
  color: white;
}

.fc {
  /* the calendar root */
  max-width: 1100px;
  margin: 0 auto;
}

.connection-card-standalone {
  min-width: 325px;
}

.menu-header {
  padding: 16px 16px 0px 24px;
}

.menu-header:hover {
  cursor: pointer;
}

.menu-header-facility {
  color: white;
  font-weight: bold;
}
.menu-header-name {
  color: #ffffffa6;
}

